	@Output() selectClick: EventEmitter<any> = new EventEmitter();//单机事件
	display1: boolean = true;
	display2: boolean = false;
	dia: boolean = false;
	resultList: any;
	code: any;
	BdAchievementGrow: any;
	BdAchievementDetail: any;
	msgs: Message[] = []; //提示信息
	Grow1: any;
	Grow2: any;
