	amountC0: any = [];
	sameLevelAverAmountC0: any = [];
	sameLevelOrderMaxC0: any = [];
	sameLevelOrderMinC0: any = [];
	dtC1: any = [];
	amountC1: any = [];
	sameLevelAverAmountC1: any = [];
	sameLevelOrderMaxC1: any = [];
	sameLevelOrderMinC1: any = [];
	dtC2: any = [];
	amountC2: any = [];
