import { Component, OnInit, Output, EventEmitter, Input, OnChanges } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
	selector: 'index-setting',
	templateUrl: './index-setting.component.html',
	styleUrls: ['./index-setting.component.scss'],
	providers: [Commfunc]
})
export class IndexSettingComponent implements OnInit{ 
	//表格中无数据显示的内容
	tabMesg: any = tableMessage;
	msgs: Message[] = [];
	index1: string = ">可选择的度量指标";
	index2: string = ">已选择的度量指标"
	userform: FormGroup; //校验
	power: any;
	selectPower: any = [];
	orgTreePage:boolean=false;
	treeOrgName:any;
	personPage:boolean=false;
	personName:string;
	personId:string;
	constructor(
		public commfunc: Commfunc
	) { 
		let dropDownSelect = this.commfunc.getSessionDataCH('dropDownSelect');
        this.treeOrgName = dropDownSelect.split(",")[0];
	}
	ngOnInit() {
	}
	perInfor(item) {
        this.personPage = false;
        this.personName = item[0].tellerName;
        this.personId = item[0].tellerId;
	}
	treeCall(param) {
        this.orgTreePage = false;
        this.treeOrgName = param.orgName;
	}
	orgSelect(){
		this.orgTreePage = true;
	}
	userSelect() {
		this.personPage = true;
	}
}