import { Component, OnInit, Output, EventEmitter, Input, OnChanges } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { CheckboxModule } from 'primeng/checkbox';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { RouterModule, Router } from '@angular/router';
import { QueryBdAchievementYe } from "app/pages/tzb/custom/achievement-task/bean/TaskArrangement.bean";
import { AchievementHttpService } from "app/pages/tzb/custom/achievement-task/http/achievement-search.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
@Component({
	selector: 'more-achievement',
	templateUrl: './more-achievement.component.html',
	styleUrls: ['./more-achievement.component.scss'],
	providers: [Commfunc, AchievementHttpService]
})
export class MoreAchievementComponent implements OnInit {
	queryBdAchievementYe: QueryBdAchievementYe = new QueryBdAchievementYe();
	@Output() selectClick: EventEmitter<any> = new EventEmitter();//单机事件
	@Input() inValue: any;
	display1: boolean = false;
	display2: boolean = false;
	display3: boolean = false;
	selectedValues: any[] = ['1', '2', '3'];
	selectedValues1: any[] = ['1', '2', '3'];
	msgs: Message[] = []; //提示信息
	resultList: any;
	code: any;
	BdAchievementYe: any;
	BdAchievementDetail: any;
	saveCustom: any = [];
	achievement: string;
	time: string;
	dia: boolean = false;
	AchiDetail: any;
	title: string;
	constructor(public commfunc: Commfunc, public router: Router, private httpService: AchievementHttpService) {

	}
	ngOnInit() {

		this.codeVal();
		this.BdAchievementYe = this.code['BdAchievementYe'];//我的业绩
		this.BdAchievementDetail = this.code['BdAchievementDetail'];//我的业绩

		this.queryBdAchievementYe.timeTypeId = this.inValue.timeTypeId;
		this.queryBdAchievementYe.levelNo = this.inValue.levelNo;
		this.queryBdAchievementYe.startTime = this.inValue.startTime;
		this.queryBdAchievementYe.endTime = this.inValue.endTime;
		this.queryBdAchievementYe.signType = this.inValue.signType;
		this.queryBdAchievementYe.arrType = "0";
		this.queryBdAchievementYe.relationNo = this.inValue.relationNo;
		this.queryBdAchievementYe.orgNo = this.inValue.orgNo;
		this.queryBdAchievementYe.levelNo = this.inValue.levelNo;

		if (this.inValue.signType == "1") {
			this.display1 = true;
			this.display2 = false;
		} else if (this.inValue.signType == "2") {
			this.display1 = false;
			this.display2 = true;
		}


		this.queryAlways();
		this.AchievementYe();

	}

	bv(item) {

		let param = {
			indexType: item,
			signType: this.inValue.signType,
			arrType: '0',
			timeTypeId: this.inValue.timeTypeId,
			startTime: this.inValue.startTime,
			endTime: this.inValue.endTime,
			relationNo: this.inValue.relationNo,
			orgNo: this.inValue.orgNo,
			levelNo: this.inValue.levelNo,
		}
		this.AchiDetail = param;
		this.dia = true;
		this.title = item;

		//	this.dia = true;


	}

	queryAlways() {
		let user = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
		let orgId = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;
		let param = {
			listShowsIdType: '1',
			listShowsIdModule: 'myPFM',
			createOrg: orgId,
			createdByUserLogin: user
		}
		this.httpService.selectUserListShow(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.selectedValues = JSON.parse(data.exploreConditionObject).myPFM;

			}
		})
	}

	//码值
	codeVal() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	AchievementYe() {

		this.httpService.queryBdAchievementYe(this.queryBdAchievementYe).subscribe(data => {
			if (data.returnCode.code == "success") {
				this.resultList = data.resultList;
				if (this.inValue.signType == "2") {
					for (let i of this.resultList) {
						let obj = {}
						obj['program'] = i.program;
						obj['sameLevelOrder'] = i.sameLevelOrder;
						obj['opt'] = {
							tooltip: {
								formatter: "{a} <br/>{b}:{c}%"
							},
							toolbox: {

							},
							series: [
								{
									name: '业务指标',
									type: "gauge",
									detail: { formatter: '{value}%' },
									data: [{ value: parseFloat(i.yearTask), name: '完成率' }]
								}
							]
						}
						this.saveCustom.push(obj)
					}
				}

				// this.msgs = [];
				// this.msgs.push({ severity: 'success', summary: '提示', detail: "任务查询成功！" });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}

	iconSwitch() {
		if (this.inValue.signType == "1") {
			this.display1 = !this.display1;
			this.display3 = !this.display3;
		} else if (this.inValue.signType == "2") {
			this.display2 = !this.display2;
			this.display3 = !this.display3;
		}


	}
	check(item) {
		if (this.selectedValues.length) {
			if (this.selectedValues.length > 3) {
				for (var i = 0; i < this.selectedValues.length; i++) {
					if (i > 2) {
						this.selectedValues[i] = undefined;
						if (this.selectedValues[i] == undefined) {
							this.selectedValues.splice(i, 1);
							i--;
						}
					}
				}
				this.msgs.push({ severity: 'error', summary: '提示', detail: "只能选中三个显示在页面中！" });
			} else {
				if (this.selectedValues[i] != undefined) {
					this.selectedValues.push(this.selectedValues[i]);
				}
			}
		} else {
			this.msgs.push({ severity: 'error', summary: '提示', detail: "请选这其中的显示在页面中！" });
		}
	}


	select() {
		let user = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
		let orgId = JSON.parse(this.commfunc.getSessionData('commonHeader')).orgId;
		let param = {
			listShowsIdType: '1',
			listShowsIdModule: 'myPFM',
			createOrg: orgId,
			createdByUserLogin: user,
			exploreConditionObject: JSON.stringify({ myPFM: this.selectedValues })
		}
		this.httpService.addUserListShow(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.selectClick.emit(this.selectedValues);
			}
		})
	}
}