import { Component, OnInit, Output, EventEmitter, Input, OnChanges } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { QueryBdAchievementDetail } from "app/pages/tzb/custom/achievement-task/bean/TaskArrangement.bean";
import { AchievementHttpService } from "app/pages/tzb/custom/achievement-task/http/achievement-search.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
@Component({
	selector: 'view-ratio',
	templateUrl: './view-ratio.component.html',
	styleUrls: ['./view-ratio.component.scss'],
	providers: [Commfunc, AchievementHttpService]

})
export class ViewRatioComponent implements OnInit {
	queryBdAchievementDetail: QueryBdAchievementDetail = new QueryBdAchievementDetail();
	BdAchievementDetail: any;
	@Input() inValue: any;
	display: boolean = false;
	msgs: Message[] = []; //提示信息
	Acde: any;
	code: any;
	MyRatio: any;
	DepartmentRatio: any;
	contributionC: any = [];
	secondProductC: any = [];
	contributionC1: any = [];
	secondProductC1: any = [];
	array1: any = [];
	array2: any = [];
	array1Name: any = [];
	array2Name: any = [];
	LineContrast: any;
	array1Value: any = [];
	array2Value: any = [];
	ArrayD: any = [
		//一级目录
		{ "sel": 'L1001', name: '授信业务产品' },
		{ "sel": 'L1002', name: '存款业务产品' },
		{ "sel": 'L1003', name: '理财业务产品' },
		{ "sel": 'L1004', name: '银行卡' },
		{ "sel": 'L1005', name: '国际业务产品' },
		//二级目录
		{ "sel": 'L2001', name: '一般贷款' },
		{ "sel": 'L2002', name: '小本贷款' },
		{ "sel": 'L2004', name: '时贷通' },
		{ "sel": 'L2005', name: '自助卡' },

		{ "sel": 'L2006', name: '消费信贷' },
		{ "sel": 'L2007', name: '承兑' },
		{ "sel": 'L2008', name: '保函' },
		{ "sel": 'L2009', name: '贴现' },

		{ "sel": 'L2010', name: '委托贷款' },
		{ "sel": 'L2048', name: 'E融卡' },
		{ "sel": 'L2011', name: '金添储蓄' },
		{ "sel": 'L2012', name: '个人活期' },

		{ "sel": 'L2013', name: '单位活期存款' },
		{ "sel": 'L2014', name: '单位定期存款' },
		{ "sel": 'L2015', name: '保证金存款' },
		{ "sel": 'L2016', name: '其他存款' },

		{ "sel": 'L2017', name: '理财产品' },
		{ "sel": 'L2018', name: '“周周盈”稳进账理财计划' },
		{ "sel": 'L2019', name: '机构理财计划' },
		{ "sel": 'L2003', name: '借记卡类授信' },

		{ "sel": 'L2020', name: '信用卡类授信' },
		{ "sel": 'L2021', name: '准贷记卡' },
		{ "sel": 'L2022', name: '出口贸易融资' },
		{ "sel": 'L2023', name: '进口贸易融资' },

		{ "sel": 'L2024', name: '一般贷款' },
		{ "sel": 'L2025', name: '国内信用证融资' },
		{ "sel": 'L2026', name: '国际结算' },
		{ "sel": 'L2046', name: '结售汇业务' },

		{ "sel": 'L2047', name: '国际业务存款' },
	];
	ArrayName: any = [];
	ConDw0: string;
	ConDw1: string;
	constructor(public commfunc: Commfunc, public router: Router, private httpService: AchievementHttpService) {

	}
	ngOnInit() {
		this.codeVal();
		this.BdAchievementDetail = this.code['BdAchievementDetail'];//我的业绩
		this.queryBdAchievementDetail.indexType = this.inValue.indexType;
		this.queryBdAchievementDetail.signType = this.inValue.signType;
		this.queryBdAchievementDetail.arrType = this.inValue.arrType;
		this.queryBdAchievementDetail.timeTypeId = this.inValue.timeTypeId;
		this.queryBdAchievementDetail.startTime = this.inValue.startTime;
		this.queryBdAchievementDetail.endTime = this.inValue.endTime;
		this.queryBdAchievementDetail.relationNo = this.inValue.relationNo;
		this.queryBdAchievementDetail.orgNo = this.inValue.orgNo;
		this.queryBdAchievementDetail.levelNo = this.inValue.levelNo;
		if (this.inValue.indexType == "1") {
			this.ConDw0 = "我的存款日均";
			this.ConDw1 = "第一名的存款日均";
		} else if (this.inValue.indexType == "2") {
			this.ConDw0 = "我的贷款日均";
			this.ConDw1 = "第一名的贷款日均";
		} else if (this.inValue.indexType == "3") {
			this.ConDw0 = "我的信用卡透支余额";
			this.ConDw1 = "第一名的信用卡透支余额";
		} else if (this.inValue.indexType == "4") {
			this.ConDw0 = "我的FTP利润";
			this.ConDw1 = "第一名的FTP利润";
		}
		this.Detail();
	}
	Detail() {
		this.httpService.queryBdAchievementDetail(this.queryBdAchievementDetail).subscribe(data => {
			if (data.returnCode.code == "success") {


				if (data.resultList) {
					for (let i of data.resultList) {
						// this.Array = ['L2004', 'L2001']
						// for (var i = 0; i < this.Array.length; i++) {
						// 	for (var j = 0; j < this.ArrayD.length; j++) {
						// 		if (this.Array[i] == this.ArrayD[j].sel) {
						// 			console.log(this.ArrayD[j].name);
						// 		}
						// 	}
						// }
						let obj = {};
						obj['value'] = i.contribution;

						for (let k of this.ArrayD) {
							if (i.secondProduct == k.sel) {
								this.secondProductC.push(k.name);
								this.array1Name.push(k.name);
								obj['name'] = k.name;
							}
						}
						//	console.log(this.secondProductC);
						this.contributionC.push(i.contribution);
						// this.secondProductC.push(i.secondProduct);


						this.array1.push(obj);

						this.array1Value.push(i.contribution);
					}

				}
				if (data.resultListFist) {
					for (let j of data.resultListFist) {

						let obj = {};
						obj['value'] = j.contribution;

						for (let l of this.ArrayD) {
							if (j.secondProduct == l.sel) {
								this.secondProductC1.push(l.name);
								this.array2Name.push(l.name);
								obj['name'] = l.name;
							}
						}
						this.contributionC1.push(j.contribution);
						//	this.secondProductC1.push(j.secondProduct);
						this.array2.push(obj);
						//this.array2Name.push(j.secondProduct);
						this.array2Value.push(j.contribution);

					}
				}

				if (this.secondProductC1.length >= this.secondProductC1) {
					this.ArrayName = this.secondProductC1;
				} else {
					this.ArrayName = this.secondProductC;
				}


				//console.log(this.secondProductC1)
				this.Det();
				this.Line();
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}

	Line() {

		this.LineContrast = {

			title: {
				text: '产品种类',
				textStyle: {
					fontSize: '12',
				},
				// x: '20px',
				// y: '5px',
				// subtext: '查看详情>'
			},
			tooltip: {
				trigger: 'axis',
				axisPointer: {
					type: 'shadow'
				}
			},
			legend: {
				data: [this.ConDw0, this.ConDw1],
			},
			grid: {
				left: '3%',
				right: '4%',
				bottom: '3%',
				containLabel: true
			},
			xAxis: {
				name: '贡献',
				scale: true,
				textStyle: {
					fontSize: '10',
				},
				type: 'value'

			},
			yAxis: {
				type: 'category',
				data: this.ArrayName,
			},
			series: [
				{
					name: this.ConDw0,
					type: 'bar',
					data: this.contributionC,
				},
				{
					name: this.ConDw1,
					type: 'bar',
					data: this.contributionC1
				}

			]

		}
	}


	Det() {
		this.MyRatio = {
			tooltip: {
				trigger: 'item',
				formatter: "{a} <br/>{b}:{c}({d}%)"
			},

			legend: {
				orient: 'vertical',
				x: 'center',
				//y:'300px',
				y: 'right',
				data: this.array1Name,
			},
			series: [
				{
					name: '我的',
					type: 'pie',
					radius: '70%',
					center: ['50%', '50%'],
					data: this.array1,
					itemStyle: {
						// emphasis: {
						shadowBlur: 10,
						shadowOffset: 0,
						shadowColor: 'rgba(0,0,0,.5)'
						// }
					},
					labelLine: {
						length: 0
					}
				}
			]
		};

		this.DepartmentRatio = {
			tooltip: {
				trigger: 'item',
				formatter: "{a} <br/>{b}:{c}({d}%)"
			},
			// title: {
			// text: '部门存款余额',
			// textStyle: {
			// 	fontSize: '12',
			// },
			// x: '20px',
			// y: '5px',
			// subtext: '查看详情>'
			// },
			legend: {
				orient: 'vertical',
				x: 'center',
				//y:'300px',
				y: 'right',
				data: this.array2Name,
			},
			series: [
				{
					name: '部门的',
					type: 'pie',
					radius: '70%',
					center: ['50%', '50%'],
					data: this.array2,
					itemStyle: {
						// emphasis: {
						shadowBlur: 10,
						shadowOffset: 0,
						shadowColor: 'rgba(0,0,0,.5)'
						// }
					},
					labelLine: {
						length: 0
					}
				}
			]
		};
	}

	//码值
	codeVal() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	iconSwitch() {
		this.display = !this.display;
	}


}