import { Component, OnInit, Output, EventEmitter, Input, OnChanges } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { QueryBdAchievementDetail, QueryBdAchievementFtp } from "app/pages/tzb/custom/achievement-task/bean/TaskArrangement.bean";
import { AchievementHttpService } from "app/pages/tzb/custom/achievement-task/http/achievement-search.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
@Component({
	selector: 'balance-view',
	templateUrl: './balance-view.component.html',
	styleUrls: ['./balance-view.component.scss'],
	providers: [Commfunc, AchievementHttpService]
})
export class BalanceViewComponent implements OnInit {
	queryBdAchievementDetail: QueryBdAchievementDetail = new QueryBdAchievementDetail();
	queryBdAchievementFtp: QueryBdAchievementFtp = new QueryBdAchievementFtp();
	@Input() inValue: any;
	firstProduct:any;
	ArrayD: any = [
		//一级目录
		{ "sel": 'L1001', name: '授信业务产品' },
		{ "sel": 'L1002', name: '存款业务产品' },
		{ "sel": 'L1003', name: '理财业务产品' },
		{ "sel": 'L1004', name: '银行卡' },
		{ "sel": 'L1005', name: '国际业务产品' },
		//二级目录
		{ "sel": 'L2001', name: '一般贷款' },
		{ "sel": 'L2002', name: '小本贷款' },
		{ "sel": 'L2004', name: '时贷通' },
		{ "sel": 'L2005', name: '自助卡' },

		{ "sel": 'L2006', name: '消费信贷' },
		{ "sel": 'L2007', name: '承兑' },
		{ "sel": 'L2008', name: '保函' },
		{ "sel": 'L2009', name: '贴现' },

		{ "sel": 'L2010', name: '委托贷款' },
		{ "sel": 'L2048', name: 'E融卡' },
		{ "sel": 'L2011', name: '金添储蓄' },
		{ "sel": 'L2012', name: '个人活期' },

		{ "sel": 'L2013', name: '单位活期存款' },
		{ "sel": 'L2014', name: '单位定期存款' },
		{ "sel": 'L2015', name: '保证金存款' },
		{ "sel": 'L2016', name: '其他存款' },

		{ "sel": 'L2017', name: '理财产品' },
		{ "sel": 'L2018', name: '“周周盈”稳进账理财计划' },
		{ "sel": 'L2019', name: '机构理财计划' },
		{ "sel": 'L2003', name: '借记卡类授信' },

		{ "sel": 'L2020', name: '信用卡类授信' },
		{ "sel": 'L2021', name: '准贷记卡' },
		{ "sel": 'L2022', name: '出口贸易融资' },
		{ "sel": 'L2023', name: '进口贸易融资' },

		{ "sel": 'L2024', name: '一般贷款' },
		{ "sel": 'L2025', name: '国内信用证融资' },
		{ "sel": 'L2026', name: '国际结算' },
		{ "sel": 'L2046', name: '结售汇业务' },

		{ "sel": 'L2047', name: '国际业务存款' },
	];
	display1: boolean = true;
	display2: boolean = false;
	display3: boolean = false;
	display4: boolean = false;
	display5: boolean = true;
	display6: boolean = false;
	Acde: any;
	secDet: any;
	code: any;
	tableResult0: any = [];
	tableResult1: any = [];
	BdAchievementDetail: any;
	msgs: Message[] = []; //提示信息
	amountC: any = [];
	amountC1: any = [];
	secondProductC: any = [];
	itemType: string;
	secondProduct: string;
	resultList1: string;
	secondProductC1: any = [];
	constructor(public commfunc: Commfunc, public router: Router, private httpService: AchievementHttpService) {

	}
	ngOnInit() {
		this.codeVal();
		this.BdAchievementDetail = this.code['BdAchievementDetail'];//我的业绩

		this.queryBdAchievementFtp.indexType = this.inValue.indexType;
		this.queryBdAchievementFtp.signType = this.inValue.signType;
		this.queryBdAchievementFtp.arrType = this.inValue.arrType;
		this.queryBdAchievementFtp.timeTypeId = this.inValue.timeTypeId;
		this.queryBdAchievementFtp.startTime = this.inValue.startTime;
		this.queryBdAchievementFtp.endTime = this.inValue.endTime;
		this.queryBdAchievementFtp.relationNo = this.inValue.relationNo;
		this.queryBdAchievementFtp.orgNo = this.inValue.orgNo;
		this.queryBdAchievementFtp.levelNo = this.inValue.levelNo;

		this.queryBdAchievementDetail.indexType = this.inValue.indexType;
		this.queryBdAchievementDetail.signType = this.inValue.signType;
		this.queryBdAchievementDetail.arrType = this.inValue.arrType;
		this.queryBdAchievementDetail.timeTypeId = this.inValue.timeTypeId;
		this.queryBdAchievementDetail.startTime = this.inValue.startTime;
		this.queryBdAchievementDetail.endTime = this.inValue.endTime;
		this.queryBdAchievementDetail.relationNo = this.inValue.relationNo;
		this.queryBdAchievementDetail.orgNo = this.inValue.orgNo;
		this.queryBdAchievementDetail.levelNo = this.inValue.levelNo;
		if (this.inValue.indexType == "4") {
			this.display5 = true;
			this.display1 = false;
		} else {
			this.display1 = true;
			this.display5 = false;
		}

		this.Detail();
	}
	iconSwitch() {
		if (this.queryBdAchievementDetail.indexType == "4") {
			this.display1 = false;
			this.display5 = !this.display5;
			this.display3 = !this.display3;
		} else {
			this.display5 = false;
			this.display1 = !this.display1;
			this.display2 = !this.display2;
		}
	}

	Detail() {
		this.httpService.queryBdAchievementDetail(this.queryBdAchievementDetail).subscribe(data => {
			if (data.returnCode.code == "success") {
				for (var i = 0; i < data.resultList.length; i++) {
					this.amountC.push(data.resultList[i].amount);

					for (var j = 0; j < this.ArrayD.length; j++) {
						if (data.resultList[i].secondProduct == this.ArrayD[j].sel) {
							this.secondProductC.push(this.ArrayD[j].name);
							data.resultList[i].secondProduct = this.ArrayD[j].name;
							this.tableResult0 = data.resultList;
						}
					}
					this.Det();
				}
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}

	Det() {
		this.Acde = {
			color: ['#37c27f'],
			title: {
				text: '(单位.万元)',
				textStyle: {
					fontSize: '12',
				},
				x: '20px',
				y: '5px',

			},
			tooltip: {
				trigger: 'axis',
				axisPointer: {
					type: 'shadow'
				}
			},
			xAxis: [
				{
					type: 'category',
					data: this.secondProductC,
				}
			],
			yAxis: [
				{
					type: 'value',
					data: [0, 20, 40, 60, 80]
				}
			],
			series: [
				{
					color: ['green'],
					data: this.amountC,
					type: 'bar',
					//设置圆柱的宽度
					barWidth: 30
				}
			]
		};
	}


	Fir(col) {
		this.display4 = true;
		this.secondProduct = col.secondProduct;
		for (var l = 0; l < this.ArrayD.length; l++) {
			if (this.secondProduct == this.ArrayD[l].name) {
				this.queryBdAchievementFtp.firstProduct = this.ArrayD[l].sel;

				this.httpService.queryBdAchievementFtp(this.queryBdAchievementFtp).subscribe(data => {
					if (data.returnCode.code == "success") {
						// this.resultList1 = data.resultList;
						for (var n = 0; n < data.resultList.length; n++) {
							for (var m = 0; m < this.ArrayD.length; m++) {
								if (data.resultList[n].secondProduct == this.ArrayD[m].sel) {
									data.resultList[n].secondProduct = this.ArrayD[m].name;
									this.resultList1 = data.resultList;
								}
							}
						}
						this.msgs = [];
						this.msgs.push({ severity: 'success', summary: '提示', detail: "任务查询成功！" });
					} else {
						this.msgs = [];
						this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
					}
				}, error => {
					this.msgs = [];
					this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
				})

			}
		}
	}

	clo() {
		this.display4 = false;
	}
	sedLine() {
		this.display6 = false;
	}

	First(secondProduct) {
		if (secondProduct) {
			this.display6 = true;
		}
		for (var i = 0; i < this.ArrayD.length; i++) {
			if (this.ArrayD[i].name == secondProduct) {
				this.queryBdAchievementFtp.firstProduct = this.ArrayD[i].sel;
			}
		}
		this.amountC1 = [];
		this.secondProductC1 = [];
		this.httpService.queryBdAchievementFtp(this.queryBdAchievementFtp).subscribe(data => {
			if (data.returnCode.code == "success") {

				for (var n = 0; n < data.resultList.length; n++) {
					this.amountC1.push(data.resultList[n].amount);

					for (var m = 0; m < this.ArrayD.length; m++) {
						if (data.resultList[n].secondProduct == this.ArrayD[m].sel) {
							data.resultList[n].secondProduct = this.ArrayD[m].name;
						}
					}

					this.secondProductC1.push(data.resultList[n].secondProduct);
				}
				this.SecDet();
				this.msgs = [];
				this.msgs.push({ severity: 'success', summary: '提示', detail: "任务查询成功！" });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})



	}

	SecDet() {
		this.secDet = {
			color: ['#37c27f'],
			title: {
				text: '(单位.万元)',
				textStyle: {
					fontSize: '12',
				},
				x: '20px',
				y: '5px',

			},
			tooltip: {
				trigger: 'axis',
				axisPointer: {
					type: 'shadow'
				}
			},
			xAxis: [
				{
					type: 'category',
					data: this.secondProductC1,
				}
			],
			yAxis: [
				{
					type: 'value',
					data: [0, 20, 40, 60, 80]
				}
			],
			series: [
				{
					color: ['green'],
					data: this.amountC1,
					type: 'bar',
					//设置圆柱的宽度
					barWidth: 30
				}
			]
		};
	}

	//码值
	codeVal() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}


}