import { Component, OnInit, Output, EventEmitter, Input, OnChanges } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { QueryBdAchievementDetail, QueryBdAchievementFtp } from "app/pages/tzb/custom/achievement-task/bean/TaskArrangement.bean";
import { AchievementHttpService } from "app/pages/tzb/custom/achievement-task/http/achievement-search.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
@Component({
    selector: 'grow-detail',
    templateUrl: './grow-detail.component.html',
    styleUrls: ['./grow-detail.component.scss'],
    providers: [Commfunc, AchievementHttpService]
})
export class GrowDetailComponent implements OnInit {
    queryBdAchievementDetail: QueryBdAchievementDetail = new QueryBdAchievementDetail();
    @Input() inValue: any;
    ArrayD: any = [
        //一级目录
        { "sel": 'L1001', name: '授信业务产品' },
        { "sel": 'L1002', name: '存款业务产品' },
        { "sel": 'L1003', name: '理财业务产品' },
        { "sel": 'L1004', name: '银行卡' },
        { "sel": 'L1005', name: '国际业务产品' },
        //二级目录
        { "sel": 'L2001', name: '一般贷款' },
        { "sel": 'L2002', name: '小本贷款' },
        { "sel": 'L2004', name: '时贷通' },
        { "sel": 'L2005', name: '自助卡' },

        { "sel": 'L2006', name: '消费信贷' },
        { "sel": 'L2007', name: '承兑' },
        { "sel": 'L2008', name: '保函' },
        { "sel": 'L2009', name: '贴现' },

        { "sel": 'L2010', name: '委托贷款' },
        { "sel": 'L2048', name: 'E融卡' },
        { "sel": 'L2011', name: '金添储蓄' },
        { "sel": 'L2012', name: '个人活期' },

        { "sel": 'L2013', name: '单位活期存款' },
        { "sel": 'L2014', name: '单位定期存款' },
        { "sel": 'L2015', name: '保证金存款' },
        { "sel": 'L2016', name: '其他存款' },

        { "sel": 'L2017', name: '理财产品' },
        { "sel": 'L2018', name: '“周周盈”稳进账理财计划' },
        { "sel": 'L2019', name: '机构理财计划' },
        { "sel": 'L2003', name: '借记卡类授信' },

        { "sel": 'L2020', name: '信用卡类授信' },
        { "sel": 'L2021', name: '准贷记卡' },
        { "sel": 'L2022', name: '出口贸易融资' },
        { "sel": 'L2023', name: '进口贸易融资' },

        { "sel": 'L2024', name: '一般贷款' },
        { "sel": 'L2025', name: '国内信用证融资' },
        { "sel": 'L2026', name: '国际结算' },
        { "sel": 'L2046', name: '结售汇业务' },

        { "sel": 'L2047', name: '国际业务存款' },
    ];
    BdAchievementDetail: any;
    code: any;
    msgs: Message[] = []; //提示信息
    orgOrdUser: string;
    tableResult0: any = [];
    constructor(public commfunc: Commfunc, public router: Router, private httpService: AchievementHttpService) {

    }

    ngOnInit() {
        this.codeVal();
        this.BdAchievementDetail = this.code['BdAchievementDetail'];//我的业绩
        this.orgOrdUser = this.inValue.orgOrdUser;
        this.queryBdAchievementDetail.levelNo = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.operatingLevel;
        if (JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.managePermission == "Y") {
            this.queryBdAchievementDetail.orgNo = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.orgId;
        } else if (JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.managePermission == "N") {
            this.queryBdAchievementDetail.relationNo = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        }
        this.queryBdAchievementDetail.indexType = this.inValue.indexType;
        this.queryBdAchievementDetail.signType = this.inValue.signType;
        this.queryBdAchievementDetail.arrType = this.inValue.arrType;
        this.queryBdAchievementDetail.timeTypeId = this.inValue.timeTypeId;
        this.queryBdAchievementDetail.startTime = this.inValue.startTime;
        this.queryBdAchievementDetail.endTime = this.inValue.endTime;
        this.queryBdAchievementDetail.relationNo = this.inValue.relationNo;
        this.queryBdAchievementDetail.orgNo = this.inValue.orgNo;
        this.queryBdAchievementDetail.levelNo = this.inValue.levelNo;
        this.Detail();
    }




    Detail() {
        this.httpService.queryBdAchievementDetail(this.queryBdAchievementDetail).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.tableResult0 = data.resultList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    //码值
    codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

}